import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { CounterService } from '../counter.service';
@Component({
  selector: 'app-product',
  templateUrl: './product.component.html',
  styleUrls: ['./product.component.css']
})
export class ProductComponent implements OnInit {

	p: number = 1;
	arrProducts: string [];
	sortOption =['name','price'];
	selectedOption: string;
	count: number;
	Price : number;

  constructor(private httpService : HttpClient, private counterService: CounterService) { }

  nextCount() {
	this.counterService.nextCount();
  }

  increase_quantity(temp_product){
	this.counterService.increase_quantity(temp_product);
  }



  ngOnInit() {
    this.httpService.get('./assets/products.json').subscribe(
      data => {
        this.arrProducts = data as string [];

      },

    );

}

}
