import { Validator,NG_VALIDATORS, AbstractControl} from '@angular/forms';
import { Directive, Input } from '@angular/core';

@Directive({
  selector: '[appPhoneMatchValidator]',
providers:[{
provide: NG_VALIDATORS,
useExisting:PhoneMatchValidatorDirective,
multi: true
}]
})
export class PhoneMatchValidatorDirective implements Validator {
@Input() appPhoneMatchValidator : string;
validate(control : AbstractControl): {[key:string]:any} | null{
const controlCompare =control.parent.get(this.appPhoneMatchValidator);

if(controlCompare && controlCompare.value !==control.value){
return {'notEqual' : true};
}
return null;
}

}